import { github } from './constMap';

export const getGithubToken = () => {
  return github.token;
};

export const getGithubOwner = () => {
  return github.user;
};

export const getGithubRepo = () => {
  return github.repository;
};

export const staticalyPath = (branch: string, path: string) => {
  return `https://cdn.staticaly.com/gh/${github.user}/${github.repository}@${branch}/${path}`;
};

export const chinaJsDelivrPath = (branch: string, path: string) => {
  return `https://jsd.cdn.zzko.cn/gh/${github.user}/${github.repository}@${branch}/${path}`;
};

export const jsDelivrPath = (branch: string, path: string) => {
  return `https://cdn.jsdelivr.net/gh/${github.user}/${github.repository}@${branch}/${path}`;
};

export const gitHubPath = (branch: string, path: string) => {
  return `https://github.com/${github.user}/${github.repository}/raw/{${branch}/${path}`;
};

export const getFileSuffix = (name: string) => {
  return name.lastIndexOf('.') > 0 ? (name.substring(name.lastIndexOf('.') + 1).toLowerCase()) : null;
};

export const getFileSize = (value?: string) => {
  if (!value) { return '0 B'; }
  const unitArr = ['B', 'KB', 'MB', 'GB', 'TB', 'PB', 'EB', 'ZB', 'YB'];
  let index = 0;
  const valuesize = parseFloat(value);
  index = Math.floor(Math.log(valuesize) / Math.log(1024));
  const size = +(valuesize / (1024 ** index)).toFixed(2);
  return `${size} ${unitArr[index]}`;
};
