import request from "../utils/request";
import { github } from '../utils/constMap';
import { getGithubOwner, getGithubRepo } from '../utils/tools';

/**
 * 素材库 是基于指定的一个github仓库作为素材库基池
 * 不同分支作为不同的库存在
 * 每个分支支持以下几种类型存储数据
 * 1.图片
 * 2.专题(文件夹)
 */

// 获取分支列表
export const getBranchList = () => {
  const owner = getGithubOwner();
  const repo = getGithubRepo();
  return request.get({
    url: `${github.host}/repos/${owner}/${repo}/branches`,
  });
};

// 获取指定路径 Path 下的目录和文件
export const getContent = (branch: string, path?: string) => {
  const owner = getGithubOwner();
  const repo = getGithubRepo();
  return request.get({
    url: `${github.host}/repos/${owner}/${repo}/contents` + `${path ? ('/' + path) : ''}`,
    data: {
      // 防止浏览器缓存
      'no-cache': Date.now(),
      ref: branch,
    }
  });
};

// 删除指定路径 Path 下的目录和文件
export const deleteContent = (branch: string, path: string, sha: string) => {
  const owner = getGithubOwner();
  const repo = getGithubRepo();
  return request.delete({
    url: `${github.host}/repos/${owner}/${repo}/contents/${path}`,
    data: {
      owner,
      repo,
      path,
      sha,
      ref: branch,
    }
  });
};

