import * as server from '../services/api';
import storage from '@/utils/storage';

const storageKey = {
  librarys: '__mytab_pics_librarys__',
};

// 初始化素材库
export const initLibrarys = async () => {
  const storageResult = storage.get(storageKey.librarys, { list: [] });
  let result = storageResult.list;
  if (result.length === 0 || Date.now() - storageResult.at >= 86400000) {
    result = await server.getBranchList() || [];
    if (!result?.message) {
      storage.set(storageKey.librarys, {
        at: Date.now(),
        list: result,
      });
    }
  }
  return result;
};

// 重新获取素材库
export const refreshLibrarys = async () => {
  const result = await server.getBranchList() || [];
  if (!result?.message) {
    storage.set(storageKey.librarys, {
      at: Date.now(),
      list: result,
    });
  }
  return result;
};

// 获取指定库的文件列表数据
export const getFileList = async (library: string, path?: string) => {
  const result = await server.getContent(library, path) || [];
  return result;
};
