import { useState, useCallback, useEffect, useRef, useImperativeHandle } from 'react';

const debounce = (fn: any, delay = 600, runFirstFn = true) => {
  let timer: any = null;
  return (...rest: any) => {
    // 清除定时器
    clearTimeout(timer);
    if (runFirstFn) {
      fn.apply(this, rest);
      runFirstFn = false;
      return;
    }
    // 设置定时器
    timer = setTimeout(fn.bind(this, ...rest), delay);
  };
};

const observerDomResize = (dom: any, callback: any) => {
  const MutationObserver =
    (window as any).MutationObserver ||
    (window as any).WebKitMutationObserver ||
    (window as any).MozMutationObserver;
  const observer = new MutationObserver(callback);
  observer.observe(dom, {
    attributes: true,
    attributeFilter: ['style'],
    attributeOldValue: true
  });
  return observer;
};

const useAutoResize = (ref: any) => {
  const [state, setState] = useState({ width: 0, height: 0 });
  const domRef = useRef(null);
  const setWH = useCallback(() => {
    const { clientWidth, clientHeight } = domRef.current || { clientWidth: 0, clientHeight: 0 };
    setState({ width: clientWidth, height: clientHeight });
  }, []);

  useImperativeHandle(ref, () => ({ setWH }), []);

  useEffect(() => {
    const debounceSetWHFun = debounce(setWH, 100);
    debounceSetWHFun();
    const domObserver = observerDomResize(domRef.current, debounceSetWHFun);
    window.addEventListener('resize', debounceSetWHFun);
    return () => {
      window.removeEventListener('resize', debounceSetWHFun);
      if (!domObserver) {
        return;
      }
      domObserver.disconnect();
      domObserver.takeRecords();
    };
  }, []);

  return { ...state, domRef, setWH };
};

export default useAutoResize;
