import React from 'react';
import {
  Button,
  Tag,
  Dropdown,
  Checkbox,
} from 'antd';
import {
  PlaySquareFilled,
  PictureFilled,
  AudioFilled,
  FolderOpenFilled,
  FileFilled,
  MoreOutlined,
} from '@ant-design/icons';
import { shallow } from 'zustand/shallow';
import { useStore } from '@/module/store';
import classnames from 'classnames';
import FileCover from '../FileCover';
import { getFileSuffix, getFileSize } from '../../utils/tools';
import { fileTypeList } from '../../utils/constMap';
import styles from './index.less';

interface IProps {
  data: any;
  onClick: () => void;
}

const selector = (store: any) => ({
  curLibrary: store.curLibrary,
});

function Item({ data, onClick }: IProps) {
  const { curLibrary } = useStore(selector, shallow);

  const menuProps = {
    items: [{
      key: 'rename',
      label: '重命名',
    }, {
      key: 'remove',
      label: '删除',
    }],
    onClick: (e: any) => {
      console.log('click', e);
    },
  };

  // 文件后缀
  const fileSuffix = data.type === 'file' ? getFileSuffix(data.name) : null;
  // 文件大小
  const fileSize = getFileSize(data.size);
  // 文件类型
  const fileType = data.type === 'dir' ? 'dir' : fileTypeList[fileSuffix || ''] || 'unknown';
  let fileTypeIcon = null;
  if (fileType === 'dir') {
    fileTypeIcon = <FolderOpenFilled style={{ color: "#fa8c16" }} />;
  } else if (fileType === 'image') {
    fileTypeIcon = <PictureFilled style={{ color: "#2f54eb" }} />;
  } else if (fileType === 'video') {
    fileTypeIcon = <PlaySquareFilled style={{ color: "#2f54eb" }} />;
  } else if (fileType === 'audio') {
    fileTypeIcon = <AudioFilled style={{ color: "#2f54eb" }} />;
  } else {
    fileTypeIcon = <FileFilled style={{ color: "#bfbfbf" }} />;
  }

  return (
    <div className={styles.item}>
      <div className={classnames(styles.wrap, data.type === 'dir' && styles.dir)} onClick={onClick}>
        <div className={styles.panel}>
          <div className={styles.cover}>
            <div className={styles.view}>
              <FileCover data={data} />
            </div>
          </div>
          <div className={styles.info}>
            <div className={styles.title}>{data.name}</div>
            <div className={styles.attr}>
              <div className={styles.tags}>
                {fileSuffix && <Tag color="geekblue">{fileSuffix.toUpperCase()}</Tag>}
                {data.type !== 'dir' && <Tag color="purple">{fileSize}</Tag>}
              </div>
              <div>{fileTypeIcon}</div>
            </div>
          </div>
          <div className={styles.more}>
            <Dropdown menu={menuProps} placement="topRight" getPopupContainer={triggerNode => triggerNode!.parentElement!.parentElement!}>
              <Button size="small" icon={<MoreOutlined rotate={90} />} />
            </Dropdown>
          </div>
          <div className={styles.mult}>
            <Checkbox />
          </div>
        </div>
      </div>
    </div>
  );
}

export default Item;
