import React, { useState } from 'react';
import { Button, Input, Radio, Checkbox } from 'antd';
import type { RadioChangeEvent } from 'antd';
import type { CheckboxValueType } from 'antd/es/checkbox/Group';
import { CloudUploadOutlined } from '@ant-design/icons';
import Modal from '../Modal';
import styles from './index.less';

function UploadFileBtn() {
  const initValue = {
    name: '',
    setting: 'none',
    options: [],
  };

  const [open, setOpen] = useState<boolean>(false);
  const [loading, setLoading] = useState<boolean>(false);
  const [value, setValue] = useState<any>(initValue);

  const showModal = () => setOpen(true);

  const handleCancel = () => setOpen(false);
  const handleOk = () => {
    console.log(value);
    setLoading(true);
    setTimeout(() => {
      setLoading(false);
      setOpen(false);
      setValue(initValue);
    }, 3000);
  };

  const onInputChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
    if (!loading) {
      setValue({ ...value, name: e.target.value });
    }
  };

  const onRadioChange = (e: RadioChangeEvent) => {
    console.log('radio checked', e.target.value);
    if (!loading) {
      setValue({ ...value, setting: e.target.value });
    }
  };

  const onCheckboxChange = (checkedValues: CheckboxValueType[]) => {
    console.log('checked = ', checkedValues);
    if (!loading) {
      setValue({ ...value, options: checkedValues });
    }
  };

  return (
    <>
      <Button type="primary" icon={<CloudUploadOutlined />} onClick={showModal}>文件上传</Button>
      <Modal
        open={open}
        title="文件上传"
        okText="上传全部"
        cancelText="取消"
        onOk={handleOk}
        onCancel={handleCancel}
        confirmLoading={loading}
        width={700}
      >
        <div className={styles.list}>
          {['', '', '', '', '', '', ''].map((i, index) => (
            <div className={styles.item} key={index}>
              <div className={styles.cover}>
                <img src="https://xhzy-test.oss-cn-hangzhou.aliyuncs.com/mediaxmaterial/2179c52b10d8b17ba3b7892c635a63a2/2023/0823/thumb/164e4c7763194c7bb9e99452eed73d68/00001.jpg" />
              </div>
              <div className={styles.info}>
                <div className={styles.title}>
                  <div className={styles.label}>文件名称</div>
                  <div className={styles.value}>
                    <Input placeholder="请输入文件名" onChange={onInputChange} value={value.name} addonAfter=".1693730169938.webp" />
                  </div>
                </div>
                <div className={styles.actions}>
                  <div className={styles.action}>
                    <div className={styles.label}>名称设置</div>
                    <div className={styles.value}>
                      <Radio.Group onChange={onRadioChange} value={value.setting}>
                        <Radio value="none">无</Radio>
                        <Radio value="hash">哈希化</Radio>
                        <Radio value="time">时间戳</Radio>
                      </Radio.Group>
                    </div>
                  </div>
                  <div className={styles.action}>
                    <div className={styles.label}>图片设置</div>
                    <div className={styles.value}>
                      <Checkbox.Group options={[
                        { label: '水印', value: 'watermark' },
                        { label: '压缩', value: 'compress' },
                      ]} value={value.options} onChange={onCheckboxChange} />
                    </div>
                  </div>
                </div>
              </div>
            </div>
          ))}
        </div>
      </Modal>
    </>
  );
}

export default UploadFileBtn;
