import React from 'react';
import { Breadcrumb } from 'antd';
import { HomeOutlined } from '@ant-design/icons';
import { shallow } from 'zustand/shallow';
import { useStore } from '@/module/store';

const selector = (store: any) => ({
  curPaths: store.curPaths,
  setCurPaths: (curPaths: string[]) => store.setStore({ key: 'curPaths', value: curPaths }),
});

function BreadcrumbBar() {
  const {
    curPaths,
    setCurPaths,
  } = useStore(selector, shallow);

  const handleBreadcrumbItemClick = (pathname: any) => {
    setCurPaths(curPaths.slice(0, curPaths.lastIndexOf(pathname) + 1));
  };

  const paths = [''].concat(curPaths);
  const items = paths.map((i, index) => {
    // eslint-disable-next-line no-script-url
    const item: any = { title: i, href: '', onClick: (e: any) => { e.preventDefault(); handleBreadcrumbItemClick(i); } };
    if (i === '') { item.title = <HomeOutlined />; }
    if (index === paths.length - 1) {
      delete item.onClick;
      delete item.href;
    }
    return item;
  });

  return curPaths.length === 0 ? <div /> : <Breadcrumb items={items} />;
}

export default BreadcrumbBar;
