import React, { useEffect } from 'react';
import classnames from 'classnames';
import { ConfigProvider, Button, Dropdown } from 'antd';
import { HddFilled, MoreOutlined, SyncOutlined, PlusOutlined } from '@ant-design/icons';
import { shallow } from 'zustand/shallow';
import { useStore } from '@/module/store';
import styles from './menu.less';

const selector = (store: any) => ({
  curLibrary: store.curLibrary,
  librarys: store.librarys,
  searchFileListLoading: store.searchFileListLoading,
  updateCurLibrary: (curLibrary: string) => store.setStore({ key: 'curLibrary', value: curLibrary }),
});

function View() {
  const { curLibrary, librarys, searchFileListLoading, updateCurLibrary } = useStore(selector, shallow);

  const menuProps = {
    items: [{
      key: 'rename',
      label: '重命名',
    }, {
      key: 'remove',
      label: '删除',
    }],
    onClick: (e: any) => {
      console.log('click', e);
    },
  };

  const handleClick = (_curLibrary: string) => {
    if (_curLibrary === curLibrary) { return false; }
    if (searchFileListLoading) { return false; }
    updateCurLibrary(_curLibrary);
  };

  useEffect(() => {
    if (librarys.length > 0) {
      handleClick(librarys[0].name);
    }
  }, [librarys]);

  return (
    <div className={styles.container}>
      <ConfigProvider autoInsertSpaceInButton={false} theme={{ token: { borderRadius: 2, colorPrimary: '#2f54eb' } }}>
        <div className={styles.header}></div>
        <div className={styles.list}>
          <div className={styles.title}>仓库列表</div>
          {librarys.map((i: any, index: string) => (
            <div
              key={index}
              className={classnames(styles.item, curLibrary === i.name && styles.select)}
              onClick={() => handleClick(i.name)}
            >
              <div className={styles.wrap}>
                <div className={styles.name}>
                  <span className={styles.icon}><HddFilled /></span>
                  <span>{i.name}</span>
                </div>
                <div className={styles.actions}>
                  <Dropdown menu={menuProps} placement="bottomRight" getPopupContainer={triggerNode => triggerNode!.parentElement!}>
                    <Button type="text" size="small" icon={<MoreOutlined rotate={90} />} />
                  </Dropdown>
                </div>
              </div>
            </div>
          ))}
        </div>
        <div className={styles.footer}>
          <PlusOutlined />
          <SyncOutlined />
        </div>
      </ConfigProvider>
    </div>
  );
}

export default View;
