import React, { useState, useEffect } from 'react';
import { Spin } from 'antd';
import { shallow } from 'zustand/shallow';
import { useStore } from '@/module/store';
import * as controller from '../module/controller';
import Main from '@/module/view/main';
import Menu from '@/module/view/menu';
import styles from './app.less';

const selector = (store: any) => ({
  initLibrarys: (librarys: any[]) => store.setStore({ key: 'librarys', value: librarys }),
});

function App() {
  const [initLoading, setInitLoading] = useState<boolean>(true);
  const store = useStore(selector, shallow);
  useEffect(() => {
    controller.initLibrarys().then(librarys => {
      if (!librarys?.message) {
        store.initLibrarys(librarys || []);
      }
      setInitLoading(false);
    });
  }, []);
  return (
    <Spin spinning={initLoading}>
      <main className={styles.main}>
        <div className={styles.layout}>
          <div className={styles.left}>
            <Menu />
          </div>
          <div className={styles.right}>
            <Main />
          </div>
        </div>
      </main>
    </Spin>
  );
}

export default App;
