import React from 'react';
import {
  FolderTwoTone,
  FileWordTwoTone,
  FileTextTwoTone,
  FileUnknownTwoTone,
  FilePdfTwoTone,
  FilePptTwoTone,
  FileZipTwoTone,
  FileExcelTwoTone,
  FileImageTwoTone,
  FileMarkdownTwoTone,
  VideoCameraTwoTone,
  CustomerServiceTwoTone,
} from '@ant-design/icons';
import { shallow } from 'zustand/shallow';
import { useStore } from '@/module/store';
import classnames from 'classnames';
import { staticalyPath, getFileSuffix } from '../../utils/tools';
import { fileTypeList } from '../../utils/constMap';
import styles from './index.less';

interface IProps {
  data: any;
}

const selector = (store: any) => ({
  curLibrary: store.curLibrary,
});

function FileCover({ data }: IProps) {
  const { curLibrary } = useStore(selector, shallow);

  const renderTypeCover = (type: string) => {
    const iconType: any = {
      'dir': <FolderTwoTone twoToneColor="#597ef7" />,
      'image': <FileImageTwoTone twoToneColor="#597ef7" />,
      'video': <VideoCameraTwoTone twoToneColor="#597ef7" />,
      'audio': <CustomerServiceTwoTone twoToneColor="#597ef7" />,
      'pdf': <FilePdfTwoTone twoToneColor="#597ef7" />,
      'excel': <FileExcelTwoTone twoToneColor="#597ef7" />,
      'doc': <FileWordTwoTone twoToneColor="#597ef7" />,
      'txt': <FileTextTwoTone twoToneColor="#597ef7" />,
      'zip': <FileZipTwoTone twoToneColor="#597ef7" />,
      'ppt': <FilePptTwoTone twoToneColor="#597ef7" />,
      'markdown': <FileMarkdownTwoTone twoToneColor="#597ef7" />,
      'unknown': <FileUnknownTwoTone twoToneColor="#597ef7" />,
    };
    return (
      <div className={styles.thumbImage}>
        {iconType[type] || iconType.unknown}
      </div>
    );
  };

  // 文件后缀
  const fileSuffix = data.type === 'file' ? getFileSuffix(data.name) : null;
  // 文件类型
  const fileType = data.type === 'dir' ? 'dir' : fileTypeList[fileSuffix || ''] || 'unknown';
  let fileCover = renderTypeCover(fileType);
  if (fileType === 'image') {
    fileCover = <img src={staticalyPath(curLibrary, data.path)} />;
  }

  return (
    <div className={styles.view}>
      {fileCover}
    </div>
  );
}

export default FileCover;
