import React, { useState, useEffect, forwardRef } from 'react';
import { ConfigProvider, Spin, Pagination } from 'antd';
import { shallow } from 'zustand/shallow';
import { useStore } from '@/module/store';
import * as controller from '../controller';
import Breadcrumb from '../components/BreadcrumbBar';
import ToolBar from '../components/ToolBar';
import Item from '../components/Item';
import useAutoResize from '../utils/useAutoResize';
import styles from './main.less';

const pageSize = 30;

const selector = (store: any) => ({
  curLibrary: store.curLibrary,
  fileList: store.fileList,
  loading: store.searchFileListLoading,
  curPaths: store.curPaths,
  setLoading: (loading: boolean) => store.setStore({ key: 'searchFileListLoading', value: loading }),
  setFileList: (fileList: string) => store.setStore({ key: 'fileList', value: fileList }),
  setCurPaths: (curPaths: string[]) => store.setStore({ key: 'curPaths', value: curPaths }),
});

const View = forwardRef(({ }, ref) => {
  const [pageNum, setPageNum] = useState<number>(1);
  const { width, domRef } = useAutoResize(ref);

  const {
    curLibrary,
    fileList,
    loading,
    curPaths,
    setFileList,
    setLoading,
    setCurPaths,
  } = useStore(selector, shallow);

  const search = async () => {
    if (loading) { return null; }
    setLoading(true);
    controller.getFileList(curLibrary, curPaths.join('/') || null).then(result => {
      setFileList(result);
      setLoading(false);
    });
  };

  useEffect(() => {
    if (curLibrary) { search(); }
  }, [curLibrary, curPaths]);

  const handleUpdatePageNum = (pageNum: number) => {
    setPageNum(pageNum);
  };

  const handleItemClick = (data: any) => {
    if (data.type === 'dir') {
      setCurPaths([...curPaths, data.name]);
    }
  };

  return (
    <div className={styles.container}>
      <ConfigProvider autoInsertSpaceInButton={false} theme={{ token: { borderRadius: 2, colorPrimary: '#2f54eb' } }}>
        <div className={styles.header}>
          <Breadcrumb />
          <ToolBar
            onRefresh={search}
          />
        </div>
        <div className={styles.content} ref={domRef} style={{ '--files-items-col': Math.floor((width - 24) / 260) || 1 } as any}>
          <Spin spinning={loading}>
            <div className={styles.list}>
              {
                fileList.slice((pageNum - 1) * pageSize, pageNum * pageSize)
                  .map((i: any, index: string) => <Item key={index} data={i} onClick={() => handleItemClick(i)} />)
              }
            </div>
            <div className={styles.pagination}>
              <Pagination
                defaultPageSize={pageSize}
                defaultCurrent={1}
                current={pageNum}
                total={fileList.length}
                onChange={handleUpdatePageNum}
                hideOnSinglePage
              />
            </div>
          </Spin>
        </div>
      </ConfigProvider>
    </div>
  );
});

export default View;
